import React, { useState, useEffect } from 'react'
import './storage.less'
import moment from 'moment'
import { storageList, unmountStorage, getCloudListAll, mountStorage, refundMsg, refundPage, refund } from '@/api/storage'
import { getCommon } from "@/api/common";
import { useNavigate } from "react-router-dom";
import { Select, Input, Table, Popover, Modal, Alert, Row, Col, Button } from 'antd';
import { showDialog } from '@/utils/util';
import {
  EllipsisOutlined
} from '@ant-design/icons';
import ComPagination from '@/components/comPagination/ComPagination.jsx'
import DelDialog from '@/components/delDialog/DelDialog';

import { useTranslation } from "react-i18next";

export default function Storage() {
  const { t } = useTranslation()
  const navigate = useNavigate()
  const { Option } = Select;
  const { TextArea } = Input;

  // 实例列表
  const [cloudList, setCloudList] = useState([])
  // 删除/卸载弹窗
  const [isModalVisible, setIsModalVisible] = useState(false)
  // 挂载弹窗
  const [isMountVisible, setIsMountVisible] = useState(false)
  const [modalText, setModalText] = useState("")
  const [modalTitle, setModalTitle] = useState("")
  const [modalData, setModalData] = useState({})
  const [errMsg, setErrMsg] = useState("")
  const [total, setTotal] = useState(0)
  const [params, setParams] = useState({
    keywords: '',
    page: 1,
    limit: 20,
    orderby: 'id',
    sort: 'desc',
  })
  const [mountData, setMountData] = useState({
    id: "",
    host_id: ""
  })

  const [size, setSize] = useState()

  // 停用页面信息
  const [refundData, setRefundData] = useState()
  // 停用页面 是否显示
  const [isShowRefund, setIsShowRefund] = useState(false)
  // 停用参数
  const [refundParams, setRefundParams] = useState({
    host_id: '',
    suspend_reason: [],
    type: ""
  })



  // 挂载loading
  const [mountLoading, setMountLoading] = useState(false)

  const columns = [
    {
      title: "名称",
      dataIndex: 'name',
      key: 'name',
      ellipsis: true,
      width: 200
    },
    {
      title: "实例",
      dataIndex: 'cloud_name',
      key: 'cloud_name',
      ellipsis: true,
      width: 200
    },
    {
      title: "容量",
      dataIndex: 'size',
      key: 'size',
      ellipsis: true,
      width: 200,
      render: (_, record) => (
        <>
          {record.size + 'G'}
        </>
      )
    },
    {
      title: "费用",
      dataIndex: 'first_payment_amount',
      key: 'first_payment_amount',
      ellipsis: true,
      width: 200,
      render: (_, record) => (
        <>
          {com.currency_prefix ? com.currency_prefix : null} {record.first_payment_amount}/{record.billing_cycle_name}
        </>
      )
    },
    {
      title: "管理",
      dataIndex: 'id',
      key: 'id',
      width: 80,
      render: (_, record) => (
        <>
          {
            record.status == 'Active' ?
              <div className="com-opt-pop" id={'com-opt' + record.id}>
                <Popover getPopupContainer={() => document.getElementById('com-opt' + record.id)}
                  content={
                    <>
                      {
                        record.cloud_name ?
                          <p className="cloud-opt" onClick={() => unmountItem(record)}>卸载</p>
                          :
                          <p className="cloud-opt" onClick={() => mountItem(record)}>挂载</p>
                      }
                      <p className="cloud-opt" onClick={() => expansionItem(record)}>扩容</p>
                      <p className="cloud-opt" onClick={() => deleteItem(record)}>{t("common.delete")}</p>
                    </>
                  } placement="topRight">
                  <p className="opt-icon"><EllipsisOutlined /></p>
                </Popover>
              </div>
              : null
          }
        </>
      )
    },
  ]
  const [dataList, setDataList] = useState([])
  const [loading, setLoading] = useState(false)
  // 通用配置
  const [com, setcom] = useState({});

  // 方法开始

  // 获取系统信息
  const getCommonData = () => {
    getCommon().then((res) => {
      if (res.data.status === 200) {
        setcom(res.data.data);
      }
    });
  }

  // 磁盘产品列表
  const getStorageList = () => {
    setLoading(true)
    storageList(params).then(res => {
      if (res.data.status === 200) {
        setDataList(res.data.data.list)
        setTotal(res.data.data.count)
      }
      setLoading(false)
    })
  }
  // 换页 换每页条数
  const changePage = (page, limit) => {
    setParams(params => {
      return { ...params, page, limit }
    })
  }

  // 挂载按钮点击 显示 挂载弹窗
  const mountItem = (record) => {
    // 展示挂载弹窗
    setIsMountVisible(true)
    // 清空错误提示
    setErrMsg("")
    // 初始化弹窗数据
    setMountData({
      id: record.id,
      host_id: cloudList[0] ? cloudList[0].id : ''
    })
  }

  const changeCloud = (e) => {
    setMountData({ ...mountData, host_id: e })
  }

  // 执行挂载
  const doMountStorage = () => {
    let isPass = true
    if (!mountData.host_id) {
      isPass = false
      setErrMsg("请选择需要挂载的实例")
    }

    if (isPass) {
      setMountLoading(true)
      const params = {
        id: mountData.id,
        host_id: mountData.host_id
      }
      mountStorage(params).then(res => {
        setMountLoading(false)
        if (res.data.status === 200) {
          getStorageList()
          showDialog("挂载成功", "success")
          setIsMountVisible(false)
        }
      }).catch(error => {
        setMountLoading(false)
        setErrMsg(error.data.msg)
      })
    }
    console.log(mountData);
    setMountLoading(false)
  }

  // 卸载
  const unmountItem = (record) => {
    setModalTitle(`从实例中卸载${record.name}`)
    setModalText(`警告：允许中的实例强制卸载磁盘可能会导致数据丢失
    或者卸载失败，建议您在实例中停止读写后卸载`)
    setModalData({ id: record.id })
    setIsModalVisible(true)
  }
  // 扩容
  const expansionItem = (record) => {
    navigate(`/expansion_storage?id=${record.id}&package_id=${record.package_id}&size=${record.size}`)
    // 跳转到磁盘扩容页面
  }

  // 删除
  const deleteItem = (record) => {
    // 磁盘的id
    const id = record.id
    setSize(record.size)
    // 初始化退款弹窗提交数据
    setRefundParams({
      host_id: id,
      suspend_reason: [],
      type: ""
    })

    const params = {
      id: Number(id)
    }
    // 1、获取产品停用信息 判断是否已申请停用 等信息
    refundMsg(params).then(res => {
      if (res.data.data.refund) {
        showDialog("已申请过停用", "warning")
      } else {
        // 2、如果未申请过停用 获取停用页面信息并展示停用页面申请弹窗
        refundPage({ host_id: id }).then(res => {
          let list = res.data.data
          setRefundData(list)
          if (list.allow_refund === 1) {  // 允许退款
            // 展示退款页面
            setIsShowRefund(true)
          } else {
            showDialog(t('common.tip1'), "warning")
          }
        })
      }
    })
  }

  // 关闭 删除卸载
  const closeDialog = () => {
    setIsModalVisible(false)
  }

  // 前往创建页面
  const toCreate = () => {
    navigate('/create_storage')
  }

  // 提交卸载
  const submitDel = () => {
    // 调用卸载
    const params = {
      id: modalData.id
    }
    unmountStorage(params).then(res => {
      console.log(res);
      if (res.data.status === 200) {
        showDialog("卸载成功", "success")
        // 关闭弹窗
        setIsModalVisible(false)
        // 重新拉取数据
        getStorageList()
      }
    }).catch(error => {
      showDialog(error.data.msg, "error")
    })
  }

  // 获取实例列表
  const doGetCloudList = () => {
    getCloudListAll().then(res => {
      if (res.data.status === 200) {
        const list = res.data.data.list.reverse()
        setCloudList(list)
      }
    })
  }


  // 停用原因选择框
  const getReason = (e) => {
    setRefundParams({ ...refundParams, suspend_reason: e })
  }
  // 自己填原因
  const getSelfReason = (e) => {
    setRefundParams({ ...refundParams, suspend_reason: e.target.value })
  }
  // 停用时间 选择框
  const getType = (e) => {
    setRefundParams({ ...refundParams, type: e })
  }
  // 确认停用申请
  const doRefund = () => {
    let isPass = true
    if (refundParams.suspend_reason.length === 0) {
      showDialog((refundData.reason_custom === 1 ? t('input') : t('select')) + t('common.stop_reason'), "error")
      return
    }
    if (!refundParams.type) {
      showDialog(t('select') + t('common.stop_time'), "error")
      return
    }
    if (isPass) {
      refund(refundParams).then(res => {
        if (res.data.status === 200) {
          showDialog("申请停用成功，请耐心等待审核", "success")
          setIsShowRefund(false)
        }
      })
    }

  }


  useEffect(() => {
    getCommonData()
    getStorageList()
    doGetCloudList()
  }, [params])

  return (
    <div className="com-content storage-list">
      <div className="com-card">
        <div className="com-top">
          <div className="top-title">磁盘</div>
          <div className="top-btn" onClick={toCreate}>创建磁盘</div>
        </div>
        <Table columns={columns} dataSource={dataList} loading={loading}
          rowKey={record => record.id} pagination={false} className="cloud-tab"
        />
        <ComPagination total={total} page={params} changePage={changePage} ></ComPagination>
      </div>
      {/* 删除/卸载 */}
      {
        isModalVisible && <DelDialog
          closeDialog={closeDialog} submitDel={submitDel}
          text={modalText}
          title={modalTitle}
        ></DelDialog>
      }
      {/* 挂载磁盘到实例 */}
      <Modal
        visible={isMountVisible}
        footer={null}
        centered={true}
        closable={false}
        style={{ borderRadius: "10px", overflow: "auto" }}
        getContainer={false}
        onCancel={() => setIsMountVisible(false)}
      >
        <div className='create-snapshot'>
          <div className="top">
            <div className="title">
              挂载磁盘到实例
            </div>
          </div>
          <div className="main">
            {
              errMsg ?
                <div className="err">
                  <Alert description={errMsg} type="error" showIcon />
                </div>
                :
                <div className="remind-text">

                </div>
            }
            <div className="form">
              <div className="row">
                <Row>
                  <Col span={4}>
                    <span className='label'>选择实例</span>
                  </Col>
                  <Col span={20}>
                    <Select size='large'
                      onChange={changeCloud}
                      placeholder="请选择实例"
                      defaultValue={cloudList[0] ? cloudList[0].id : ''}
                    >
                      {
                        cloudList.map(item => {
                          return (<Option value={item.id} key={item.id}>{item.name}</Option>)
                        })
                      }
                    </Select>
                  </Col>
                </Row>
              </div>
            </div>
          </div>
          <div className="footer">
            <Button className='cancel-btn' onClick={() => setIsMountVisible(false)}>取消</Button>
            <Button className='sub-btn' onClick={doMountStorage} loading={mountLoading}>确定</Button>
          </div>
        </div>
      </Modal>
      {/* 停用弹窗 */}
      {
        com && refundData ?
          <Modal
            className='refund-modal'
            visible={isShowRefund}
            footer={null}
            centered={true}
            closable={false}
            style={{ borderRadius: "10px", overflow: "auto" }}
            getContainer={false}
            onCancel={() => setIsShowRefund(false)}
          >
            <div className="modal-unopen">
              <div className="modal-title">{t('common.apply_refund')}</div>
              <div className="modal-form">
                {/* 产品信息 */}
                <div className="config-item">
                  <div className="row-l">{t('common.product_info')}</div>
                  <div className="row-r">
                    {/* 产品配置 */}
                    <div className="item">
                      <p className="l-tit">{t('common.product_config')}：</p>
                      <div className="r-text">
                        {size ? size + 'G' : null}
                      </div>
                    </div>
                    {/* 订购时间 */}
                    <div className="item">
                      <p className='l-tit'>{t('common.buy_time')}：</p>
                      <div className="r-text">
                        <p>{moment(refundData.host.create_time * 1000).format("YYYY-MM-DD HH:mm")}</p>
                      </div>
                    </div>
                    {/* 订购金额 */}
                    <div className="item">
                      <p className='l-tit'>{t('common.buy_money')}：</p>
                      <div className="r-text">
                        <p>{com.currency_prefix}{refundData.host.first_payment_amount}{com.currency_code}</p>
                      </div>
                    </div>
                  </div>
                </div>
                {/* 停用原因 */}
                <div className="config-item">
                  <div className="row-l">{t('common.stop_reason')}</div>
                  <div className="row-r">
                    {
                      refundData.reason_custom !== 1 ?
                        <Select mode="multiple" showArrow allowClear size='large' style={{ width: "100%" }} onChange={getReason}>
                          {
                            refundData.reasons.map(item => {
                              return <Option key={item.id} value={item.id}>{item.content}</Option>
                            })
                          }
                        </Select>
                        :
                        <TextArea onChange={getSelfReason} rows={4} placeholder="请输入停用原因" />
                    }
                  </div>
                </div>
                {/* 停用时间 */}
                <div className="config-item">
                  <div className="row-l row-center">
                    {t('common.stop_time')}
                  </div>
                  <div className="row-r">
                    <Select size='large' style={{ width: "100%" }} onChange={getType}>
                      <Option value="Expire">{t('common.expire')}</Option>
                      <Option value="Immediate">{t('common.immediate')}</Option>
                    </Select>
                  </div>
                </div>
                {/* 退款金额 */}
                <div className="config-item money">
                  <div className="row-l">
                    {t('common.refund_amount')}
                  </div>
                  <div className="row-right">

                    {
                      refundParams.type === "Immediate" ?
                        // 立即停用
                        com.currency_prefix + refundData.host.amount + com.currency_code
                        :
                        com.currency_prefix + '0' + com.currency_code
                      // 到期停用
                    }
                  </div>
                </div>
              </div>
              <div className="modal-footer">
                <Button className='sure' onClick={doRefund}>
                  {t('common.sure_refund')}
                </Button>
                <Button className='cancel' onClick={() => setIsShowRefund(false)}>{t('common.cancel')}</Button>
              </div>
            </div>
          </Modal>
          : null
      }
    </div>
  )
}
