// 存储（磁盘）
import request from '../utils/request'

/* 魔方云磁盘产品列表 */
export function storageList(params) {
    return request.get(`/idcsmart_cloud_disk`, { params });
}

// 挂载磁盘
export function mountStorage(params) {
    return request.put(`/idcsmart_cloud_disk/${params.id}/mount`, params);
}
// 卸载磁盘
export function unmountStorage(params) {
    return request.put(`/idcsmart_cloud_disk/${params.id}/umount`, params);
}
// 删除磁盘
export function delStorage(params) {
    return request.delete(`/idcsmart_cloud_disk/${params.id}`, params);
}
// 扩容磁盘
export function expansionStorage(params) {
    return request.put(`/idcsmart_cloud_disk/${params.id}/expansion`, params);
}

// 获取磁盘升降级配置
export function diskPackage(params) {
    return request.get(`/idcsmart_cloud_disk/${params.id}/package`, { params });
}

// 停用页面信息
export function refundPage(params) {
    return request.get(`/refund`, { params });
}
// 获取停用产品信息
export function refundMsg(params) {
    return request.get(`/refund/host/${params.id}/refund`, { params });
}
// 停用
export function refund(params) {
    return request.post(`/refund`, params);
}


/* 获取实例 */
export function getCloudListAll() {
    return request.get(`/idcsmart_cloud/all`);
}

/* 获取实例详情 */
export function getCloudById(params) {
    return request.get(`/idcsmart_cloud/${params.id}`);
}

// 结算商品
export function settle(params) {
    return request.post(`/product/settle`, params)
}

// 商品配置页面
export function getConfigOptionPage(params) {
    return request.get(`/product/${params.id}/config_option`, { params })
}

// 修改配置计算价格
export function getConfigOptionPrice(params) {
    return request.post(`/product/${params.id}/config_option`, params)
}

// 魔方云磁盘配置周期价格
export function getDurationPrice(params) {
    return request.get(`/idcsmart_cloud_disk/duration_price`, { params })
}

// 魔方云磁盘订购页面实例配置
export function configPackage(params) {
    return request.get(`/product/${params.id}/idcsmart_cloud_disk/package`, { params })
}