import { useEffect } from 'react';
import { useTranslation } from "react-i18next";
import Swal from "sweetalert2";
import './DelDialog.css'
export default function DelDialog (props) {
  const { t } = useTranslation()
  const handleOk = () => {
    props.closeDialog()
    props.submitDel()
  }
  const handleCancel = () => {
    props.closeDialog()
  }
  const showDialog = () => {
    Swal.fire({
      title: props.title ? props.title : t('common.delete'),
      text: props.text ? props.text : t('common.tip') + '？',
      customClass: {
        title: 'del-tit',
        icon: props.icon ? "warning-icon" : "del-icon",
        text: 'del-text'
      },
      icon: props.icon || "info",
      showCancelButton: true,
      confirmButtonText: t("common.sure"),
      cancelButtonText: t("common.cancel"),
    }).then(result => {
      if (result.isConfirmed) {
        handleOk()
      } else if (result.isDismissed) {
        handleCancel()
      }
    })
  }
  useEffect(() => {
    showDialog()
  }, [props])
  return null
}
